.stars-block {
  display: flex;

  &__stars {
    width: 16px;
    height: 16px;
    background: url(../images/star.svg) no-repeat;
    margin-right: 4px;
    background-position: center center;

    &:last-child {
      margin-right: 0;
    }
  }
}

.stars-block--stars5 .stars-block__stars,
.stars-block--stars1 .stars-block__stars:first-child,
.stars-block--stars2 .stars-block__stars:not(:nth-child(n+3)),
.stars-block--stars3 .stars-block__stars:not(:nth-child(n+4)),
.stars-block--stars4 .stars-block__stars:not(:nth-child(n+5)) {
  background: url(../images/star-active.svg) no-repeat;
  background-position: center center;
}
