.menu {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;

  &__item {
    list-style: none;
    font-family: $main-font;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    margin-left: 20px;

    &:first-child {
      margin: 0;
    }
  }

  &__link {
    color: $brand-color;
    text-decoration: none;
    position: relative;
    padding: 23px 0;
    display: block;

    &:hover {
      color: $brand-color2;
    }

    &.is-active,
    &:active {
      color: $brand-color2;

      &::after {
        content: " ";
        height: 4px;
        background: $brand-color2;
        width: 100%;
        border-radius: 8px;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
  }
}
